<template>
        <v-btn @click.prevent="stripeCheckout" x-large id="checkout-button">
            Kreditkarte
        </v-btn>
</template>

<script>
import { loadStripe } from '@stripe/stripe-js';


export default {
    name: 'StripeCheckout',
    props: ['purchaseData'],
    methods: {
        async stripeCheckout() {
            const stripe = await loadStripe('pk_test_51Hb7OnD9k02yHazB9OIWAwGr5YrfdrW0740zHm3ExouSwoTlksqSzFHhwmnO5zyWLjbSS54X2hEkxpZGWCCUyIid00S6kF26Yj');
            console.log(this.purchaseData);
            fetch('/create-session', {
                    method: 'POST',
                    headers: { 'Content-Type' : 'application/json' },
                    body: JSON.stringify(this.purchaseData)
            })
            .then((res)     => res.json())
            .then((session) => stripe.redirectToCheckout({ sessionId: session.id }))
            .then((res)     => res.error ? alert(res.error.message) : null)
            .catch((err)    => console.error(`Error: ${err}`));
        }
    },
    created() {
    }
}
</script>


