function baumPreise(n) {
    if (0 < n && n < 10) {
        return n*8;
    } else if (10 <= n && n < 100) {
        return n*7;
    } else if (100 <= n && n < 250) {
        return n*6;
    } else if (250 <= n && n < 500) {
        return n*5;
    } else if (500 <= n && n < 1000) {
        return n*4;
    } else if (1000 <= n) {
        return n*3;
    }
}

module.exports = baumPreise;