<template>
  
  <v-app>
    
    <NavBar></NavBar>

    <v-main>

      <v-img src="./assets/Baumspitzen.jpg" class="mb-12">
        <Landing></Landing>
      </v-img>

      <v-img src="./assets/Deutscher-Wald.jpg" class="mb-12">
        <Overview id="Uebersicht"></Overview>
      </v-img>

      <v-img src="./assets/Baeume-Draufsicht.jpg">
        <v-container fill-height class="d-flex justify-center">
          
          <v-row>
            <v-col cols=12 class="d-flex justify-center">
              <Preise id="Preise"></Preise>
            </v-col>
           
            <v-col class="d-flex" id="Kaufen">
              <Kauf ref="kauf"></Kauf>
            </v-col>
           
            <v-col class="d-flex flex-column">
              
              <Forms ref="person"></Forms>

              <v-card class="rounded-xl mt-6">
                
                <v-overlay absolute opacity=0.8 :value="zahlenOverlay">
                  <v-btn x-large color="#0d7066" @click="checkPersonKauf">Zahlen</v-btn>
                </v-overlay>

                <v-container>
                  <h4 class="text-center">Wie möchtest du Zahlen?</h4>

                  <v-row>
                    <v-col class="d-flex justify-center align-center">
                      <StripeCheckout :purchase-data="lineItems"></StripeCheckout>
                    </v-col>

                    <v-col class="d-flex justify-center align-center">
                      <v-btn x-large class="px-13" @click="paypalTooltip = !paypalTooltip">PayPal</v-btn>
                      <v-overlay v-model="paypalTooltip">
                        <p>Coming Soon!</p>
                        <v-btn color="red" @click="paypalTooltip = !paypalTooltip">Schliessen</v-btn>
                      </v-overlay>
                    </v-col>
                  
                  </v-row>

                </v-container>
              </v-card>

            </v-col>
          </v-row>
          
        </v-container>
      </v-img>

      <Footer></Footer>

    </v-main>

  </v-app>

</template>

<script>
import NavBar from './components/NavBar';
import Landing from './components/Landing';
import Overview from './components/Overview';
import Preise from './components/Preise';
import Kauf from './components/Kauf';
import Forms from './components/Forms';
import StripeCheckout from './components/StripeCheckout';
import Footer from './components/Footer';
import baumPreiseID from './baumPreiseID';

export default {
  name: 'App',

  components: {
    NavBar,
    Landing,
    Overview,
    Preise,
    Kauf,
    Forms,
    StripeCheckout,
    Footer
  },

  data() {
    return {
      lineItems: [],
      personObj: null,
      kaufObj: null,
      zahlenOverlay: true,
      paypalTooltip: false
    };
  },

  mounted() {
  },

  methods: {
    checkPersonKauf() {
      if(!this.$refs.person.submit()) {
        this.personObj = this.$refs.person.content();
      }

      if(this.$refs.kauf.submit()) {
        this.kaufObj = {
          numTrees: this.$refs.kauf.numTrees,
          price: this.$refs.kauf.sum(),
          choosenCorpIDs: this.$refs.kauf.choosenCorpIDs
        }
      }

      if(this.personObj && this.kaufObj) {
        // remove overlay
        this.zahlenOverlay = false;

        // create db entry
        fetch('/create-customer', {
          method: 'POST',
          headers: { 'Content-type' : 'application/json' },
          body: JSON.stringify({
            person: this.personObj,
            kauf: this.kaufObj
          })
        }).catch((err) => console.error(err));

        // populate lineItems
        this.lineItems.push({
          price: baumPreiseID(this.kaufObj.numTrees),
          quantity: this.kaufObj.numTrees
        });

        if(this.kaufObj.choosenCorpIDs.length > 0) {
          this.lineItems.push({
            price: 'price_1HbPoZD9k02yHazB11HMJjCN',
            quantity: this.kaufObj.choosenCorpIDs.length
          });
        }
      }
    }
  }
};
</script>