<template>
    <v-card
        color="white"
        elevation="2"
        class="rounded-xl px-8"
        width="100%"
    >
        <v-container
            fill-height
            class="d-flex flex-column"
        >
            <h4 class="mt-4">Wie viele Bäume möchtest du kaufen?</h4>
            
            <v-text-field
                outlined
                color="#0D7066"
                v-model="numTrees"
                label="Anzahl Bäume"
                required
                class="pt-4 pb-8"
                :rules="[() => !!numTrees || 'Du musst mind. 1 Baum Kaufen!']"
                ref="numTrees"
            ></v-text-field>

            <h4>Welche individuellen Grußkarten möchtest du?</h4>

            <v-list rounded class="pt-4">
                <v-list-item-group
                    multiple
                    v-model="choosenCorpIDs"
                >
                    <template v-for="(corpID, i) in corpIDs">
                        <v-list-item
                            :key="`item-${i}`"
                        >
                            <template v-slot:default="{ active }">
                                <v-list-item-content>
                                    <v-list-item-title>{{ corpID }}</v-list-item-title>
                                </v-list-item-content>
                                
                                <v-list-item-action>
                                    <v-checkbox
                                        :input-value="active"
                                        color="#0D7066"
                                    ></v-checkbox>
                                </v-list-item-action>
                            </template>
                        </v-list-item>
                    </template>
                </v-list-item-group>
            </v-list>

            <h3 class="pt-8">Gesamtbetrag:</h3>

            <v-card width="75%" class="d-flex justify-center mb-4" elevation=4>
                <h1 id="sum">{{ sum() }}</h1>
            </v-card>

        </v-container>
    </v-card>
</template>

<script>
import baumPreise from '../baumPreise';

export default {
    name: 'Kauf',
    data() {
        return {
            corpIDs: [
                "Weihnachtskarte",
                "Glückwunsch: Firmenjubiläum",
                "Glückwunsch: Geburtstag (Mitarbeiter)",
                "Glückwunsch: Geburtstag (Kunde)",
                "Glückwunsch: Neutral"
            ],
            choosenCorpIDs: [],
            numTrees: undefined
        }
    },
    methods: {
        sum() {
            return baumPreise(this.numTrees) + this.choosenCorpIDs.length*285 || 0;
        },
        submit() {
            this.$refs.numTrees.validate(true);
            return this.numTrees;
        }
    }
}
</script>

<style>
#sum::after {
    content: " €";
}
</style>