<template>
    <v-sheet
        color="white"
        elevation="2"
        class="rounded-xl pa-8 d-flex align-start"
    >
        <v-form>
            <v-container fill-height>
                <v-row>
                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="firstname"
                            label="Vorname"
                            ref="firstname"
                            required
                            :rules="[() => !!firstname || 'Dieses Feld ist Pflicht!']"
                        ></v-text-field>
                    </v-col>

                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="lastname"
                            label="Nachname"
                            ref="lastname"
                            required
                            :rules="[() => !!lastname || 'Dieses Feld ist Pflicht!']"
                        ></v-text-field>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="email"
                            label="E-mail"
                            ref="email"
                            required
                            :rules="[
                                () => !!email || 'Dieses Feld ist Pflicht!',
                                () => emailRegExp.test(email) || 'Bitte gib eine gültige E-mail Addresse ein!']"
                        ></v-text-field>
                    </v-col>
                    
                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="business"
                            label="ggf. Unternehmen"
                        ></v-text-field>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col cols="12">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="address"
                            label="Strasse und Hausnummer"
                            ref="address"
                            required
                            :rules="[() => !!address || 'Dieses Feld ist Pflicht!']"
                        ></v-text-field>
                    </v-col>
                </v-row>

                <v-row>
                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="plz"
                            label="Postleitzahl"
                            ref="plz"
                            required
                            :rules="[
                                () => !!plz || 'Dieses Feld ist Pflicht!',
                                () => plzRegExp.test(plz) || 'Bitte gib eine gültige Postleitzahl ein!',
                                () => plz.length == 5 || 'Bitte gib eine gültige Postleitzahl ein!']"
                        ></v-text-field>
                    </v-col>
                    
                    <v-col cols="6">
                        <v-text-field
                            outlined
                            color="#0D7066"
                            v-model="ort"
                            label="Ort"
                            ref="ort"
                            required
                            :rules="[() => !!ort || 'Dieses Feld ist Pflicht!']"
                        ></v-text-field>
                    </v-col>
                </v-row>
            </v-container>
        </v-form>
    </v-sheet>
</template>

<script>
export default {
    name: "Forms",
    data() {
        return {
            firstname: '',
            lastname: '',
            email: '',
            business: '',
            address: '',
            plz: '',
            ort: '',
            formHasErros: false,
            emailRegExp: /.*@.*\..*/, // thanks: https://stackoverflow.com/a/55293545,
            plzRegExp: /\d{5}/ // thanks: https://stackoverflow.com/a/4975676 hmmm...
        }
    },
    computed: {
        form() {
            return {
                firstname: this.firstname,
                lastname: this.lastname,
                email: this.email,
                address: this.address,
                plz: this.plz,
                ort: this.ort
            };
        }
    },
    methods: {
        submit() {
            this.formHasErros = false;

            Object.keys(this.form).forEach((f) => {
                if(!this.form[f]) this.formHasErros = true;
                this.$refs[f].validate(true);
            });

            return this.formHasErros;
        },
        content() {
            return {
                firstname: this.firstname,
                lastname: this.lastname,
                email: this.email,
                business: this.business,
                addresse: this.address,
                plz: this.plz,
                ort: this.ort
            }
        }
    }
};
</script>